.n-wrapper {
  height: 10vh;
  display: flex;
  justify-content: space-between;
}
.n-left {
  flex: 1;
  align-items: center;
  display: flex;
  gap: 2rem;
}
.n-name {
  font-size: 1.3rem;
  font-weight: bold;
}
.n-right {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  font-weight: 400;
}
.n-list {
  flex: 10;
}
.n-list > ul {
  display: flex;
  gap: 2rem;
  margin-right: 4rem;
  list-style-type: none;
  cursor: pointer;
}
.n-list > ul :hover {
  cursor: pointer;
  color: var(--orange);
}
.n-list > ul > li:hover {
  cursor: pointer;
  color: var(--orange);
}
.button {
  font-size: 0.8rem;
  padding: 0.5rem 1rem;
}

@media screen and (max-width: 480px) {
  .n-list {
    display: none;
  }
  .n-button {
    display: none;
  }
}
